import styled from "styled-components";
import { mainStyle } from "../styles/globalStyle";

const FooterWrap = styled.div`
  padding: ${mainStyle.padding};
  height: 200px;
  background-color: #fee600;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: 500px) {
    width: 100%;
    padding: ${mainStyle.moPadding};
    margin-top: -100px;
  }
`;

const ConWrap = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: 500px) {
    padding: ${mainStyle.moPadding};
    font-size: 12px;
    text-align: center;
  }
`;

const Logo = styled.div`
  width: 180px;
  height: 48px;
  margin-right: 100px;
  @media screen and (max-width: 500px) {
    display: none;
  }
`;

const Right = styled.div`
  font-size: 16px;
  color: #071e61;
  line-height: 25px;
  @media screen and (max-width: 500px) {
    font-size: 14px;
    line-height: 20px;
  }
`;

const Copy = styled.p`
  margin-top: 40px;
  font-size: 12px;
  color: #071e61;
`;

export const Footer = () => {
  return (
    <FooterWrap>
      <ConWrap>
        <Logo
          style={{
            background: `url(https://paikdabang.com/wp-content/themes/paikdabang/assets/images/flogo.png) no-repeat center / cover`,
          }}
        ></Logo>
        <Right>
          <span>
            ()  211-86-00870  {" "}
          </span>{" "}
          <br />
          <span>
            :    1 39  5~6 : 02-511-3864{" "}
          </span>
          <br />
          <span>
            {" "}
             : 02-549-3864 : 02-3447-0410 :
            1544-2360
          </span>
        </Right>
      </ConWrap>
      <Copy>
        <p>COPYRIGHT 2022 KIMYUJIN KOREA INC. ALL RIGHTS RESERVED</p>
      </Copy>
    </FooterWrap>
  );
};
